<template>
  <v-container fill-height class="py-5 grey lighten-3">
    <v-layout justify-center>
      <v-card raised>
        <v-toolbar flat>
          <v-toolbar-title>Update Data Aturan</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-select v-model="item.suhu" :items="options.suhu" label="Suhu" />
          <v-select v-model="item.tekananUdara" :items="options.tekananUdara" label="Tekanan Udara" />
          <v-select v-model="item.kecepatanAngin" :items="options.kecepatanAngin" label="Kecepatan Angin" />
          <v-select v-model="item.kelembapan" :items="options.kelembapan" label="Kelembapan" />
          <v-select v-model="item.keterangan" :items="options.keterangan" label="Keterangan" />
        </v-card-text>
        <v-card-actions>
          <v-btn @click="update" flat dark color="green">Simpan</v-btn>
          <v-btn @click="$router.back()" flat dark color="pink">Batal</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script>
import * as options from "@/models/options";
import { Rule__findOne, Rule__update } from "@/services/rules";
import evbus from "@/evbus";

export default {
  name: 'Update',
  props: ['id'],
  data: () => ({
    item: {
      suhu: options.suhu[0],
      kecepatanAngin: options.kecepatanAngin[0],
      kelembapan: options.kelembapan[0],
      tekananUdara: options.tekananUdara[0],
      keterangan: options.keterangan[0]
    },
    options
  }),
  methods: {
    async loadData() {
      evbus.$emit("loading-on");
      try {
        const item = await Rule__findOne(this.id);
        delete item.id;
        this.item = { ...item };
        evbus.$emit("success", "Sukses memuat data");
      } catch (err) {
        evbus.$emit("error", "Gagal memuat data");
      } finally {
        evbus.$emit("loading-off");
      }
    },
    async update() {
      const payload = { ...this.item };
      evbus.$emit("loading-on");
      try {
        const item = await Rule__update(this.id, payload);
        evbus.$emit("success", "Sukses mengubah data");
      } catch (err) {
        evbus.$emit("error", "Gagal mengubah data");
      } finally {
        evbus.$emit("loading-off");
      }
    }
  },
  mounted() {
    this.loadData();
  }
}
</script>

